import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Input } from "@/components/ui/input";
import { Label } from "@/components/ui/label";
import { Textarea } from "@/components/ui/textarea";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Star, Clock, MapPin, User, Minus, Plus, ArrowLeft } from "lucide-react";
import { useParams, Link } from "react-router-dom";
import Header from "@/components/Header";
import ReviewSection from "@/components/ReviewSection";
import PaymentComponent from "@/components/PaymentComponent";
import { KlarnaPayment } from "@/components/KlarnaPayment";
import { VideoDisplay } from "@/components/VideoDisplay";
import { Dialog, DialogContent, DialogDescription, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog";
import { useToast } from "@/hooks/use-toast";

// Mock data f√∂r en specifik r√§tt
const dishData = {
  id: "1",
  title: "Mormors k√∂ttbullar",
  description: "Traditionella svenska k√∂ttbullar med gr√§dds√•s, lingonsylt och pressgurka. Receptet har g√•tt i arv i tre generationer och tillagas med k√§rlek och omsorg.",
  price: 89,
  image: "/assets/meatballs.jpg",
  rating: 4.8,
  reviews: 127,
  cookName: "Anna Lindstr√∂m",
  cookLocation: "S√∂dermalm, Stockholm",
  cookAddress: "Hornsgatan 45, 118 49 Stockholm",
  prepTime: "30 min",
  tags: ["Traditionellt", "Glutenfritt"],
  available: 5,
  ingredients: ["K√∂ttf√§rs (n√∂t & fl√§sk)", "Gr√§dde", "√Ñgg", "Str√∂br√∂d", "L√∂k", "Lingonsylt", "Pressgurka"],
  allergens: ["Gluten", "Mj√∂lk", "√Ñgg"],
  pickupTimes: ["17:30", "18:00", "18:30", "19:00", "19:30"],
  cookImage: "https://images.unsplash.com/photo-1438761681033-6461ffad8d80?w=150&h=150&fit=crop&crop=face",
  cookRating: 4.9,
  cookDescription: "Passionerad hemkock med 15 √•rs erfarenhet. Specialiserad p√• traditionell svensk husmanskost.",
  videos: [
    {
      id: "1",
      title: "S√• g√∂r jag mina k√∂ttbullar",
      description: "Steg-f√∂r-steg guide till perfekta k√∂ttbullar",
      socialUrl: "https://www.tiktok.com/@anna_kok/video/123456789",
      platform: "tiktok" as const,
      createdAt: "2024-01-15T10:00:00Z"
    },
    {
      id: "2", 
      title: "Hemlagad gr√§dds√•s",
      description: "Min hemliga gr√§dds√•s till k√∂ttbullarna",
      socialUrl: "https://www.instagram.com/p/ABC123/",
      platform: "instagram" as const,
      createdAt: "2024-01-10T14:30:00Z"
    }
  ]
};

const DishPage = () => {
  const { id } = useParams();
  const { toast } = useToast();
  const [quantity, setQuantity] = useState(1);
  const [selectedTime, setSelectedTime] = useState("");
  const [specialRequests, setSpecialRequests] = useState("");
  const [showPayment, setShowPayment] = useState(false);

  const handleQuantityChange = (change: number) => {
    const newQuantity = quantity + change;
    if (newQuantity >= 1 && newQuantity <= dishData.available) {
      setQuantity(newQuantity);
    }
  };

  const handleOrder = () => {
    if (!selectedTime) {
      toast({
        title: "V√§lj h√§mtningstid",
        description: "Du m√•ste v√§lja en tid f√∂r att h√§mta din best√§llning.",
        variant: "destructive"
      });
      return;
    }

    setShowPayment(true);
  };

  return (
    <div className="min-h-screen bg-background">
      <Header />
      
      <div className="container mx-auto px-4 py-8">
        <Link to="/" className="inline-flex items-center gap-2 text-primary hover:underline mb-6">
          <ArrowLeft className="w-4 h-4" />
          Tillbaka till startsidan
        </Link>

        <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
          {/* V√§nster kolumn - Bild och beskrivning */}
          <div>
            <div className="relative rounded-xl overflow-hidden mb-6">
              <img 
                src={dishData.image} 
                alt={dishData.title}
                className="w-full h-96 object-cover"
              />
              <div className="absolute top-4 right-4 bg-white/90 backdrop-blur-sm px-3 py-2 rounded-full">
                <div className="flex items-center gap-1">
                  <Star className="w-4 h-4 text-yellow-500 fill-current" />
                  <span className="font-medium">{dishData.rating}</span>
                  <span className="text-sm text-muted-foreground">({dishData.reviews})</span>
                </div>
              </div>
            </div>

            <div className="space-y-6">
              <div>
                <h1 className="text-3xl font-bold mb-2">{dishData.title}</h1>
                <p className="text-lg text-muted-foreground">{dishData.description}</p>
              </div>

              <div className="flex flex-wrap gap-2">
                {dishData.tags.map((tag) => (
                  <Badge key={tag} variant="secondary">{tag}</Badge>
                ))}
              </div>

              <div>
                <h3 className="text-lg font-semibold mb-3">Ingredienser</h3>
                <div className="grid grid-cols-2 gap-2">
                  {dishData.ingredients.map((ingredient) => (
                    <div key={ingredient} className="text-sm text-muted-foreground">
                      ‚Ä¢ {ingredient}
                    </div>
                  ))}
                </div>
              </div>

              <div>
                <h3 className="text-lg font-semibold mb-2">Allergener</h3>
                <div className="flex flex-wrap gap-2">
                  {dishData.allergens.map((allergen) => (
                    <Badge key={allergen} variant="outline" className="text-orange-600 border-orange-600">
                      {allergen}
                    </Badge>
                  ))}
                </div>
              </div>
            </div>
          </div>

          {/* H√∂ger kolumn - Best√§llning och kock-info */}
          <div className="space-y-6">
            {/* Kock-information */}
            <Card>
              <CardHeader>
                <div className="flex items-center gap-4">
                  <img 
                    src={dishData.cookImage} 
                    alt={dishData.cookName}
                    className="w-16 h-16 rounded-full object-cover"
                  />
                  <div>
                    <CardTitle className="text-lg">{dishData.cookName}</CardTitle>
                    <div className="flex items-center gap-2 text-sm text-muted-foreground">
                      <Star className="w-4 h-4 text-yellow-500 fill-current" />
                      <span>{dishData.cookRating} betyg</span>
                    </div>
                  </div>
                </div>
              </CardHeader>
              <CardContent>
                <p className="text-sm text-muted-foreground mb-3">{dishData.cookDescription}</p>
                <div className="flex items-center gap-2 text-sm text-muted-foreground">
                  <MapPin className="w-4 h-4" />
                  <span>{dishData.cookLocation}</span>
                </div>
              </CardContent>
            </Card>

            {/* Best√§llningsformul√§r */}
            <Card>
              <CardHeader>
                <CardTitle>G√∂r din best√§llning</CardTitle>
                <CardDescription>
                  V√§lj antal portioner och h√§mtningstid
                </CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <div className="flex items-center justify-between">
                  <span className="text-2xl font-bold text-primary">{dishData.price} kr</span>
                  <Badge variant="outline">{dishData.available} kvar</Badge>
                </div>

                <div className="flex items-center gap-2 text-sm text-muted-foreground">
                  <Clock className="w-4 h-4" />
                  <span>Klar om {dishData.prepTime}</span>
                </div>

                <div>
                  <Label htmlFor="quantity">Antal portioner</Label>
                  <div className="flex items-center gap-3 mt-2">
                    <Button 
                      variant="outline" 
                      size="icon"
                      onClick={() => handleQuantityChange(-1)}
                      disabled={quantity <= 1}
                    >
                      <Minus className="w-4 h-4" />
                    </Button>
                    <span className="w-12 text-center font-medium">{quantity}</span>
                    <Button 
                      variant="outline" 
                      size="icon"
                      onClick={() => handleQuantityChange(1)}
                      disabled={quantity >= dishData.available}
                    >
                      <Plus className="w-4 h-4" />
                    </Button>
                  </div>
                </div>

                <div>
                  <Label htmlFor="pickup-time">H√§mtningstid</Label>
                  <Select value={selectedTime} onValueChange={setSelectedTime}>
                    <SelectTrigger className="mt-2">
                      <SelectValue placeholder="V√§lj tid" />
                    </SelectTrigger>
                    <SelectContent>
                      {dishData.pickupTimes.map((time) => (
                        <SelectItem key={time} value={time}>
                          {time}
                        </SelectItem>
                      ))}
                    </SelectContent>
                  </Select>
                </div>

                <div>
                  <Label htmlFor="special-requests">S√§rskilda √∂nskem√•l (valfritt)</Label>
                  <Textarea 
                    id="special-requests"
                    placeholder="T.ex. extra kryddig, utan l√∂k..."
                    className="mt-2"
                    value={specialRequests}
                    onChange={(e) => setSpecialRequests(e.target.value)}
                  />
                </div>

                <div className="space-y-3 pt-4 border-t">
                  <div className="flex justify-between items-center">
                    <span>Totalt ({quantity} portioner)</span>
                    <span className="text-xl font-bold">{dishData.price * quantity} kr</span>
                  </div>
                  
                  <Dialog open={showPayment} onOpenChange={setShowPayment}>
                    <DialogTrigger asChild>
                      <Button 
                        className="w-full" 
                        variant="food" 
                        size="lg"
                        onClick={handleOrder}
                      >
                        L√§gg best√§llning
                      </Button>
                    </DialogTrigger>
                    <DialogContent className="max-w-2xl max-h-[90vh] overflow-y-auto">
                      <DialogHeader>
                        <DialogTitle>V√§lj betalningss√§tt</DialogTitle>
                        <DialogDescription>
                          Slutf√∂r din best√§llning med ditt f√∂redragna betalningss√§tt
                        </DialogDescription>
                      </DialogHeader>
                      
                      <div className="space-y-6">
                        <KlarnaPayment
                          dishTitle={dishData.title}
                          dishPrice={dishData.price}
                          quantity={quantity}
                          onPaymentSuccess={() => {
                            setShowPayment(false);
                            toast({
                              title: "Best√§llning genomf√∂rd!",
                              description: "Du kommer att f√• en bekr√§ftelse via e-post"
                            });
                          }}
                        />
                        
                        <div className="relative">
                          <div className="absolute inset-0 flex items-center">
                            <span className="w-full border-t" />
                          </div>
                          <div className="relative flex justify-center text-xs uppercase">
                            <span className="bg-background px-2 text-muted-foreground">
                              Eller
                            </span>
                          </div>
                        </div>
                        
                        <PaymentComponent
                          dishTitle={dishData.title}
                          dishPrice={dishData.price}
                          quantity={quantity}
                          pickupTime={selectedTime}
                          pickupAddress={dishData.cookAddress}
                          specialRequests={specialRequests}
                        />
                      </div>
                    </DialogContent>
                  </Dialog>
                </div>

                <div className="text-center text-sm text-muted-foreground">
                  <p>H√§mtadress: {dishData.cookAddress}</p>
                </div>
              </CardContent>
            </Card>
          </div>
        </div>

        {/* Videos fr√•n kocken */}
        {dishData.videos && dishData.videos.length > 0 && (
          <div className="mt-12">
            <VideoDisplay videos={dishData.videos} />
          </div>
        )}

        {/* Recensioner */}
        <div className="mt-12">
          <ReviewSection 
            dishId={dishData.id}
            averageRating={dishData.rating}
            totalReviews={dishData.reviews}
            reviews={[]}
          />
        </div>
      </div>
    </div>
  );
};

export default DishPage;