import { useState } from 'react';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Badge } from "@/components/ui/badge";
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group";
import { Label } from "@/components/ui/label";
import { Progress } from "@/components/ui/progress";
import { 
  CheckCircle,
  AlertCircle,
  Download,
  ArrowRight,
  ArrowLeft,
  Shield
} from "lucide-react";
import { useToast } from "@/hooks/use-toast";

interface Question {
  id: string;
  question: string;
  description: string;
  options: {
    value: string;
    label: string;
    points: number;
  }[];
}

const KitchenAssessment = () => {
  const { toast } = useToast();
  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [answers, setAnswers] = useState<Record<string, string>>({});
  const [showResults, setShowResults] = useState(false);

  const questions: Question[] = [
    {
      id: "separation",
      question: "Hur separerar du verksamhet fr√•n privat anv√§ndning?",
      description: "Separation mellan verksamhet och privat anv√§ndning - i tid eller rum",
      options: [
        { value: "time", label: "Jag arbetar endast vissa tider och st√§dar mellan", points: 2 },
        { value: "space", label: "Jag har separata utrymmen/ytor f√∂r verksamheten", points: 3 },
        { value: "none", label: "Jag har ingen separation √§n", points: 0 },
        { value: "both", label: "Jag har b√•de tids- och rumsseparation", points: 3 }
      ]
    },
    {
      id: "handwashing",
      question: "Har du m√∂jlighet att tv√§tta h√§nder mellan olika moment?",
      description: "Handhygien - m√∂jlighet att tv√§tta h√§nder mellan olika moment",
      options: [
        { value: "yes_warm", label: "Ja, med varmt vatten och tv√•l n√§ra arbetsplatsen", points: 3 },
        { value: "yes_cold", label: "Ja, men endast kallt vatten", points: 1 },
        { value: "far", label: "Ja, men tv√§ttst√§lle √§r l√•ngt fr√•n k√∂ket", points: 1 },
        { value: "no", label: "Nej, jag saknar l√§mpligt tv√§ttst√§lle", points: 0 }
      ]
    },
    {
      id: "illness",
      question: "Har du rutiner f√∂r vad som g√§ller n√§r n√•gon i hemmet √§r sjuk?",
      description: "Rutiner f√∂r sjukdom - vad som g√§ller n√§r n√•gon i hemmet √§r sjuk",
      options: [
        { value: "written", label: "Ja, jag har skriftliga rutiner", points: 3 },
        { value: "mental", label: "Ja, jag vet vad som g√§ller", points: 2 },
        { value: "some", label: "Jag har delvis koll p√• detta", points: 1 },
        { value: "no", label: "Nej, jag beh√∂ver l√§ra mig mer", points: 0 }
      ]
    },
    {
      id: "cleaning",
      question: "Har du rutiner f√∂r reng√∂ring av redskap, ytor och utrustning?",
      description: "Reng√∂ringsrutiner f√∂r redskap, ytor och utrustning",
      options: [
        { value: "documented", label: "Ja, dokumenterade rutiner med schema", points: 3 },
        { value: "routine", label: "Ja, jag har fasta rutiner", points: 2 },
        { value: "basic", label: "Jag st√§dar grundligt men utan fasta rutiner", points: 1 },
        { value: "no", label: "Jag beh√∂ver utveckla b√§ttre rutiner", points: 0 }
      ]
    },
    {
      id: "family_pets",
      question: "Har du rutiner f√∂r familjemedlemmar och husdjur under verksamhet?",
      description: "Rutiner f√∂r familjemedlemmar och husdjur under verksamhet",
      options: [
        { value: "strict", label: "Ja, familj/djur har begr√§nsad tillg√•ng under arbetstid", points: 3 },
        { value: "some", label: "Jag har vissa regler men de kan f√∂rb√§ttras", points: 2 },
        { value: "informal", label: "Vi √§r f√∂rsiktiga men har inga fasta rutiner", points: 1 },
        { value: "no", label: "Jag beh√∂ver utveckla rutiner f√∂r detta", points: 0 }
      ]
    },
    {
      id: "surfaces",
      question: "Har du tillr√§ckligt med ytor f√∂r att separera r√•varor och f√§rdiga produkter?",
      description: "Tillr√§ckligt med ytor f√∂r att separera r√•varor och f√§rdiga produkter",
      options: [
        { value: "ample", label: "Ja, gott om separata ytor och sk√§rbr√§dor", points: 3 },
        { value: "adequate", label: "Ja, tillr√§ckligt med planering", points: 2 },
        { value: "tight", label: "Det fungerar men √§r tr√•ngt", points: 1 },
        { value: "insufficient", label: "Nej, jag beh√∂ver mer yta", points: 0 }
      ]
    },
    {
      id: "temperature",
      question: "Har du l√§mplig utrustning f√∂r temperaturkontroll?",
      description: "L√§mplig utrustning f√∂r temperaturkontroll och hygien",
      options: [
        { value: "complete", label: "Ja, termometrar f√∂r mat och kyl/frys", points: 3 },
        { value: "partial", label: "Jag har viss utrustning men beh√∂ver mer", points: 2 },
        { value: "basic", label: "Jag har grundl√§ggande utrustning", points: 1 },
        { value: "none", label: "Jag saknar temperaturkontroll", points: 0 }
      ]
    },
    {
      id: "haccp",
      question: "Har du gjort en HACCP-analys av din verksamhet?",
      description: "HACCP-analys av risker i din specifika verksamhet",
      options: [
        { value: "complete", label: "Ja, fullst√§ndig analys och dokumentation", points: 3 },
        { value: "started", label: "Jag har p√•b√∂rjat processen", points: 2 },
        { value: "know", label: "Jag vet vad det √§r men inte gjort det √§n", points: 1 },
        { value: "unknown", label: "Jag beh√∂ver l√§ra mig om HACCP", points: 0 }
      ]
    }
  ];

  const handleAnswerChange = (questionId: string, value: string) => {
    setAnswers(prev => ({
      ...prev,
      [questionId]: value
    }));
  };

  const calculateScore = () => {
    let totalScore = 0;
    let maxScore = 0;
    
    questions.forEach(question => {
      const answer = answers[question.id];
      const selectedOption = question.options.find(opt => opt.value === answer);
      if (selectedOption) {
        totalScore += selectedOption.points;
      }
      maxScore += Math.max(...question.options.map(opt => opt.points));
    });
    
    return { totalScore, maxScore, percentage: Math.round((totalScore / maxScore) * 100) };
  };

  const getScoreColor = (percentage: number) => {
    if (percentage >= 80) return "text-green-600";
    if (percentage >= 60) return "text-yellow-600";
    return "text-red-600";
  };

  const getScoreText = (percentage: number) => {
    if (percentage >= 80) return "Utm√§rkt! Ditt k√∂k √§r v√§l f√∂rberett.";
    if (percentage >= 60) return "Bra start! N√•gra f√∂rb√§ttringar beh√∂vs.";
    return "Du beh√∂ver g√∂ra flera f√∂r√§ndringar innan ans√∂kan.";
  };

  const nextQuestion = () => {
    if (currentQuestion < questions.length - 1) {
      setCurrentQuestion(currentQuestion + 1);
    } else {
      setShowResults(true);
    }
  };

  const prevQuestion = () => {
    if (currentQuestion > 0) {
      setCurrentQuestion(currentQuestion - 1);
    }
  };

  const downloadChecklist = () => {
    toast({
      title: "Checklista laddas ner",
      description: "Din personliga checklista har laddats ner baserat p√• dina svar.",
    });
  };

  const restartAssessment = () => {
    setCurrentQuestion(0);
    setAnswers({});
    setShowResults(false);
  };

  if (showResults) {
    const score = calculateScore();
    
    return (
      <div className="min-h-screen bg-background py-16">
        <div className="container mx-auto px-4">
          <div className="max-w-4xl mx-auto">
            <Card>
              <CardHeader className="text-center">
                <Badge variant="secondary" className="mb-4 mx-auto w-fit">
                  <Shield className="w-4 h-4 mr-2" />
                  Resultat
                </Badge>
                <CardTitle className="text-2xl">Din k√∂ksbed√∂mning</CardTitle>
                <CardDescription>
                  Baserat p√• dina svar har vi bed√∂mt hur v√§l ditt k√∂k uppfyller kraven
                </CardDescription>
              </CardHeader>
              <CardContent className="space-y-6">
                <div className="text-center">
                  <div className={`text-4xl font-bold mb-2 ${getScoreColor(score.percentage)}`}>
                    {score.percentage}%
                  </div>
                  <p className="text-muted-foreground mb-4">
                    {score.totalScore} av {score.maxScore} po√§ng
                  </p>
                  <p className={`text-lg ${getScoreColor(score.percentage)}`}>
                    {getScoreText(score.percentage)}
                  </p>
                </div>

                <div className="bg-secondary/30 rounded-lg p-6">
                  <h3 className="font-semibold mb-4">N√§sta steg:</h3>
                  <div className="space-y-3 text-sm">
                    {score.percentage >= 80 ? (
                      <div className="flex items-start gap-3">
                        <CheckCircle className="w-5 h-5 text-green-500 mt-0.5" />
                        <span>Du kan b√∂rja ans√∂ka om kommunalt tillst√•nd</span>
                      </div>
                    ) : (
                      <div className="flex items-start gap-3">
                        <AlertCircle className="w-5 h-5 text-yellow-500 mt-0.5" />
                        <span>F√∂rb√§ttra omr√•dena d√§r du fick l√•ga po√§ng innan ans√∂kan</span>
                      </div>
                    )}
                    <div className="flex items-start gap-3">
                      <Download className="w-5 h-5 text-primary mt-0.5" />
                      <span>Ladda ner den personliga checklistan baserat p√• dina svar</span>
                    </div>
                  </div>
                </div>

                <div className="flex flex-col sm:flex-row gap-4 justify-center">
                  <Button onClick={downloadChecklist} size="lg" className="bg-gradient-to-r from-primary to-accent hover:from-primary/90 hover:to-accent/90">
                    <Download className="w-5 h-5 mr-2" />
                    Ladda ner checklista
                  </Button>
                  <Button variant="outline" onClick={restartAssessment} size="lg">
                    G√∂r om bed√∂mningen
                  </Button>
                </div>
              </CardContent>
            </Card>
          </div>
        </div>
      </div>
    );
  }

  const progress = ((currentQuestion + 1) / questions.length) * 100;
  const currentQ = questions[currentQuestion];
  const currentAnswer = answers[currentQ.id];

  return (
    <div className="min-h-screen bg-background py-16">
      <div className="container mx-auto px-4">
        <div className="max-w-3xl mx-auto">
          <div className="text-center mb-8">
            <Badge variant="secondary" className="mb-4">
              <Shield className="w-4 h-4 mr-2" />
              K√∂ksbed√∂mning
            </Badge>
            <h1 className="text-3xl font-bold mb-4">Bed√∂m ditt k√∂k</h1>
            <p className="text-muted-foreground mb-6">
              Svara p√• fr√•gorna f√∂r att se hur v√§l ditt k√∂k uppfyller kommunens krav
            </p>
            <div className="space-y-2">
              <div className="flex justify-between text-sm text-muted-foreground">
                <span>Fr√•ga {currentQuestion + 1} av {questions.length}</span>
                <span>{Math.round(progress)}% klart</span>
              </div>
              <Progress value={progress} className="w-full" />
            </div>
          </div>

          <Card>
            <CardHeader>
              <CardTitle className="text-xl">{currentQ.question}</CardTitle>
              <CardDescription>{currentQ.description}</CardDescription>
            </CardHeader>
            <CardContent className="space-y-6">
              <RadioGroup 
                value={currentAnswer || ""} 
                onValueChange={(value) => handleAnswerChange(currentQ.id, value)}
              >
                {currentQ.options.map((option) => (
                  <div key={option.value} className="flex items-center space-x-2 p-3 rounded-lg border hover:bg-secondary/50 transition-colors">
                    <RadioGroupItem value={option.value} id={option.value} />
                    <Label htmlFor={option.value} className="flex-1 cursor-pointer">
                      {option.label}
                    </Label>
                  </div>
                ))}
              </RadioGroup>

              <div className="flex justify-between pt-6">
                <Button 
                  variant="outline" 
                  onClick={prevQuestion}
                  disabled={currentQuestion === 0}
                >
                  <ArrowLeft className="w-4 h-4 mr-2" />
                  F√∂reg√•ende
                </Button>
                
                <Button 
                  onClick={nextQuestion}
                  disabled={!currentAnswer}
                  className="bg-gradient-to-r from-primary to-accent hover:from-primary/90 hover:to-accent/90"
                >
                  {currentQuestion === questions.length - 1 ? "Se resultat" : "N√§sta"}
                  <ArrowRight className="w-4 h-4 ml-2" />
                </Button>
              </div>
            </CardContent>
          </Card>
        </div>
      </div>
    </div>
  );
};

export default KitchenAssessment;