import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { Textarea } from '@/components/ui/textarea';
import { Checkbox } from '@/components/ui/checkbox';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { Building, MapPin, DollarSign, Users, CheckCircle, Upload } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';
import { supabase } from '@/integrations/supabase/client';

interface KitchenData {
  businessName: string;
  contactPerson: string;
  email: string;
  phone: string;
  address: string;
  city: string;
  kitchenSize: string;
  postalCode: string;
  kitchenType: string;
  capacity: string;
  hourlyRate: string;
  description: string;
  facilities: string[];
  availability: string[];
  businessLicense: File | null;
  kitchenImages: File[];
}

export const KitchenPartnerOnboarding = () => {
  const { toast } = useToast();
  const [currentStep, setCurrentStep] = useState(1);
  const [formData, setFormData] = useState<KitchenData>({
    businessName: '',
    contactPerson: '',
    email: '',
    phone: '',
    address: '',
    city: '',
    kitchenSize: '',
    postalCode: '',
    kitchenType: '',
    capacity: '',
    hourlyRate: '',
    description: '',
    facilities: [],
    availability: [],
    businessLicense: null,
    kitchenImages: []
  });

  const facilityOptions = [
    'Professionell spis',
    'Ugn',
    'Kylsk√•p',
    'Frys',
    'Diskmaskin',
    'Arbetsytor i rostfritt st√•l',
    'Parkering',
    'Leveransomr√•de',
    'F√∂rvaring'
  ];

  const availabilityOptions = [
    'M√•ndag-Fredag morgon',
    'M√•ndag-Fredag eftermiddag',
    'M√•ndag-Fredag kv√§ll',
    'Helger',
    'Natt (22:00-06:00)',
    'Hela dygnet'
  ];

  const handleInputChange = (field: keyof KitchenData, value: string) => {
    setFormData(prev => ({ ...prev, [field]: value }));
  };

  const handleFacilityChange = (facility: string, checked: boolean) => {
    setFormData(prev => ({
      ...prev,
      facilities: checked 
        ? [...prev.facilities, facility]
        : prev.facilities.filter(f => f !== facility)
    }));
  };

  const handleAvailabilityChange = (availability: string, checked: boolean) => {
    setFormData(prev => ({
      ...prev,
      availability: checked 
        ? [...prev.availability, availability]
        : prev.availability.filter(a => a !== availability)
    }));
  };

  const handleFileUpload = (field: 'businessLicense' | 'kitchenImages', files: FileList | null) => {
    if (!files) return;
    
    if (field === 'businessLicense') {
      setFormData(prev => ({ ...prev, businessLicense: files[0] }));
    } else {
      setFormData(prev => ({ ...prev, kitchenImages: Array.from(files) }));
    }
  };

  const handleSubmit = async () => {
    // Validation
    if (!formData.businessName || !formData.contactPerson || !formData.email) {
      toast({
        title: "Obligatoriska f√§lt saknas",
        description: "Fyll i alla obligatoriska f√§lt f√∂r att forts√§tta",
        variant: "destructive"
      });
      return;
    }

    try {
      const { error } = await supabase.from('kitchen_partners').insert({
        business_name: formData.businessName,
        address: `${formData.address}, ${formData.city}`,
        kitchen_size: parseInt(formData.kitchenSize) || 0,
        hourly_rate: parseFloat(formData.hourlyRate) || 0,
        kitchen_description: formData.description,
        approved: false,
        application_status: 'pending',
        user_id: 'temp-user-id' // Detta beh√∂ver kopplas till riktig anv√§ndar-auth
      });

      if (error) {
        throw error;
      }

      toast({
        title: "Ans√∂kan skickad!",
        description: "Vi kommer att granska din ans√∂kan och √•terkomma inom 2-3 arbetsdagar"
      });
    } catch (error) {
      toast({
        title: "Fel vid skickning", 
        description: "N√•got gick fel. F√∂rs√∂k igen senare.",
        variant: "destructive"
      });
    }
  };

  const nextStep = () => {
    if (currentStep < 4) {
      setCurrentStep(currentStep + 1);
    }
  };

  const prevStep = () => {
    if (currentStep > 1) {
      setCurrentStep(currentStep - 1);
    }
  };

  return (
    <div className="min-h-screen bg-background py-8">
      <div className="container mx-auto px-4 max-w-2xl">
        <div className="text-center mb-8">
          <h1 className="text-3xl font-bold text-foreground mb-2">
            Bli K√∂kspartner
          </h1>
          <p className="text-muted-foreground">
            Hyr ut ditt restaurangk√∂k till passionerade hemmakockar och skapa extra int√§kter
          </p>
        </div>

        {/* Progress indicator */}
        <div className="flex justify-center mb-8">
          <div className="flex items-center space-x-4">
            {[1, 2, 3, 4].map((step) => (
              <div key={step} className="flex items-center">
                <div className={`w-8 h-8 rounded-full flex items-center justify-center ${
                  step <= currentStep 
                    ? 'bg-primary text-white' 
                    : 'bg-muted text-muted-foreground'
                }`}>
                  {step < currentStep ? <CheckCircle className="w-4 h-4" /> : step}
                </div>
                {step < 4 && (
                  <div className={`w-16 h-1 ${
                    step < currentStep ? 'bg-primary' : 'bg-muted'
                  }`} />
                )}
              </div>
            ))}
          </div>
        </div>

        <Card>
          <CardHeader>
            <CardTitle className="flex items-center gap-2">
              <Building className="w-5 h-5" />
              {currentStep === 1 && "Grundl√§ggande information"}
              {currentStep === 2 && "K√∂ksinformation"}
              {currentStep === 3 && "Faciliteter & Tillg√§nglighet"}
              {currentStep === 4 && "Dokument & Slutf√∂ra"}
            </CardTitle>
            <CardDescription>
              {currentStep === 1 && "Ber√§tta om ditt f√∂retag och kontaktinformation"}
              {currentStep === 2 && "Detaljer om ditt k√∂k och priser"}
              {currentStep === 3 && "Vilka faciliteter finns och n√§r √§r k√∂ket tillg√§ngligt"}
              {currentStep === 4 && "Ladda upp n√∂dv√§ndiga dokument och slutf√∂r ans√∂kan"}
            </CardDescription>
          </CardHeader>

          <CardContent className="space-y-6">
            {/* Steg 1: Grundl√§ggande information */}
            {currentStep === 1 && (
              <div className="space-y-4">
                <div>
                  <Label htmlFor="businessName">F√∂retagsnamn *</Label>
                  <Input
                    id="businessName"
                    value={formData.businessName}
                    onChange={(e) => handleInputChange('businessName', e.target.value)}
                    placeholder="T.ex. Marias Restaurang AB"
                  />
                </div>

                <div>
                  <Label htmlFor="contactPerson">Kontaktperson *</Label>
                  <Input
                    id="contactPerson"
                    value={formData.contactPerson}
                    onChange={(e) => handleInputChange('contactPerson', e.target.value)}
                    placeholder="F√∂r- och efternamn"
                  />
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div>
                    <Label htmlFor="email">E-post *</Label>
                    <Input
                      id="email"
                      type="email"
                      value={formData.email}
                      onChange={(e) => handleInputChange('email', e.target.value)}
                      placeholder="kontakt@dittforetag.se"
                    />
                  </div>
                  <div>
                    <Label htmlFor="phone">Telefon</Label>
                    <Input
                      id="phone"
                      value={formData.phone}
                      onChange={(e) => handleInputChange('phone', e.target.value)}
                      placeholder="070-123 45 67"
                    />
                  </div>
                </div>

                <div>
                  <Label htmlFor="address">Adress</Label>
                  <Input
                    id="address"
                    value={formData.address}
                    onChange={(e) => handleInputChange('address', e.target.value)}
                    placeholder="Gatuadress"
                  />
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  <div>
                    <Label htmlFor="city">Stad</Label>
                    <Input
                      id="city"
                      value={formData.city}
                      onChange={(e) => handleInputChange('city', e.target.value)}
                      placeholder="Stockholm"
                    />
                  </div>
                  <div>
                    <Label htmlFor="postalCode">Postnummer</Label>
                    <Input
                      id="postalCode"
                      value={formData.postalCode}
                      onChange={(e) => handleInputChange('postalCode', e.target.value)}
                      placeholder="123 45"
                    />
                  </div>
                </div>
              </div>
            )}

            {/* Steg 2: K√∂ksinformation */}
            {currentStep === 2 && (
              <div className="space-y-4">
                <div>
                  <Label htmlFor="kitchenType">Typ av k√∂k</Label>
                  <Select onValueChange={(value) => handleInputChange('kitchenType', value)}>
                    <SelectTrigger>
                      <SelectValue placeholder="V√§lj k√∂kstyp" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="restaurant">Restaurangk√∂k</SelectItem>
                      <SelectItem value="catering">Cateringk√∂k</SelectItem>
                      <SelectItem value="bakery">Bageri</SelectItem>
                      <SelectItem value="commercial">Kommersiellt k√∂k</SelectItem>
                      <SelectItem value="shared">Delat k√∂k</SelectItem>
                    </SelectContent>
                  </Select>
                </div>

                <div>
                  <Label htmlFor="capacity">Kapacitet (antal personer som kan arbeta samtidigt)</Label>
                  <Select onValueChange={(value) => handleInputChange('capacity', value)}>
                    <SelectTrigger>
                      <SelectValue placeholder="V√§lj kapacitet" />
                    </SelectTrigger>
                    <SelectContent>
                      <SelectItem value="1-2">1-2 personer</SelectItem>
                      <SelectItem value="3-5">3-5 personer</SelectItem>
                      <SelectItem value="6-10">6-10 personer</SelectItem>
                      <SelectItem value="10+">Fler √§n 10 personer</SelectItem>
                    </SelectContent>
                  </Select>
                </div>

                <div>
                  <Label htmlFor="hourlyRate">Timkostnad (kr/timme)</Label>
                  <Input
                    id="hourlyRate"
                    type="number"
                    value={formData.hourlyRate}
                    onChange={(e) => handleInputChange('hourlyRate', e.target.value)}
                    placeholder="250"
                  />
                </div>

                <div>
                  <Label htmlFor="description">Beskrivning av k√∂ket</Label>
                  <Textarea
                    id="description"
                    value={formData.description}
                    onChange={(e) => handleInputChange('description', e.target.value)}
                    placeholder="Beskriv ditt k√∂k, dess faciliteter och vad som g√∂r det speciellt..."
                    rows={4}
                  />
                </div>
              </div>
            )}

            {/* Steg 3: Faciliteter & Tillg√§nglighet */}
            {currentStep === 3 && (
              <div className="space-y-6">
                <div>
                  <Label className="text-base font-medium">Tillg√§ngliga faciliteter</Label>
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-3 mt-2">
                    {facilityOptions.map((facility) => (
                      <div key={facility} className="flex items-center space-x-2">
                        <Checkbox
                          id={facility}
                          checked={formData.facilities.includes(facility)}
                          onCheckedChange={(checked) => 
                            handleFacilityChange(facility, checked as boolean)
                          }
                        />
                        <Label htmlFor={facility} className="text-sm">
                          {facility}
                        </Label>
                      </div>
                    ))}
                  </div>
                </div>

                <div>
                  <Label className="text-base font-medium">Tillg√§nglighet</Label>
                  <div className="grid grid-cols-1 md:grid-cols-2 gap-3 mt-2">
                    {availabilityOptions.map((availability) => (
                      <div key={availability} className="flex items-center space-x-2">
                        <Checkbox
                          id={availability}
                          checked={formData.availability.includes(availability)}
                          onCheckedChange={(checked) => 
                            handleAvailabilityChange(availability, checked as boolean)
                          }
                        />
                        <Label htmlFor={availability} className="text-sm">
                          {availability}
                        </Label>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            )}

            {/* Steg 4: Dokument */}
            {currentStep === 4 && (
              <div className="space-y-6">
                <div>
                  <Label htmlFor="businessLicense">N√§ringstillst√•nd (valfritt)</Label>
                  <Input
                    id="businessLicense"
                    type="file"
                    accept=".pdf,.jpg,.jpeg,.png"
                    onChange={(e) => handleFileUpload('businessLicense', e.target.files)}
                  />
                  <p className="text-sm text-muted-foreground mt-1">
                    PDF, JPG eller PNG. Max 10MB.
                  </p>
                </div>

                <div>
                  <Label htmlFor="kitchenImages">Bilder p√• k√∂ket</Label>
                  <Input
                    id="kitchenImages"
                    type="file"
                    multiple
                    accept=".jpg,.jpeg,.png"
                    onChange={(e) => handleFileUpload('kitchenImages', e.target.files)}
                  />
                  <p className="text-sm text-muted-foreground mt-1">
                    V√§lj flera bilder som visar ditt k√∂k. JPG eller PNG.
                  </p>
                </div>

                <div className="bg-secondary/50 p-4 rounded-lg">
                  <h3 className="font-medium mb-2">N√§sta steg:</h3>
                  <ul className="text-sm text-muted-foreground space-y-1">
                    <li>‚Ä¢ Vi granskar din ans√∂kan inom 2-3 arbetsdagar</li>
                    <li>‚Ä¢ Du f√•r besked via e-post</li>
                    <li>‚Ä¢ Vid godk√§nnande f√•r du tillg√•ng till partner-dashboarden</li>
                    <li>‚Ä¢ D√• kan du b√∂rja ta emot bokningar fr√•n kockar</li>
                  </ul>
                </div>
              </div>
            )}

            {/* Navigation buttons */}
            <div className="flex justify-between pt-6">
              <Button 
                variant="outline" 
                onClick={prevStep}
                disabled={currentStep === 1}
              >
                Tillbaka
              </Button>

              {currentStep < 4 ? (
                <Button onClick={nextStep}>
                  N√§sta steg
                </Button>
              ) : (
                <Button onClick={handleSubmit}>
                  Skicka ans√∂kan
                </Button>
              )}
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  );
};