import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Card, CardContent } from "@/components/ui/card";
import { Search, ExternalLink, MapPin, Loader2 } from "lucide-react";
import { useToast } from "@/hooks/use-toast";
import { supabase } from "@/integrations/supabase/client";

interface MunicipalityResult {
  municipality: string;
  links: Array<{
    title: string;
    url: string;
    description: string;
  }>;
}

const MunicipalitySearch = () => {
  const { toast } = useToast();
  const [address, setAddress] = useState("");
  const [result, setResult] = useState<MunicipalityResult | null>(null);
  const [loading, setLoading] = useState(false);
  const [showApiKeyInput, setShowApiKeyInput] = useState(false);
  const [tempApiKey, setTempApiKey] = useState("");

  const searchMunicipality = async () => {
    if (!address.trim()) {
      toast({
        title: "Ange en adress",
        description: "V√§nligen ange en fullst√§ndig adress f√∂r att s√∂ka",
        variant: "destructive"
      });
      return;
    }

    setLoading(true);
    try {
      // Use mock data for now - no API cost
      const mockResult = getMockMunicipalityData(address);
      
      if (mockResult) {
        setResult(mockResult);
        toast({
          title: "S√∂kning slutf√∂rd (testdata)",
          description: `Hittade information f√∂r ${mockResult.municipality}`,
        });
      } else {
        // If no mock data found, try API if user has key
        if (tempApiKey && tempApiKey.startsWith('pplx-')) {
          return await searchWithDirectCall();
        }
        
        // Show API key input for unknown locations
        setShowApiKeyInput(true);
        toast({
          title: "Plats inte igenk√§nd",
          description: "Denna plats finns inte i testdatan. Anv√§nd en riktig API-nyckel f√∂r fullst√§ndig s√∂kning.",
          variant: "default"
        });
      }
      
    } catch (error) {
      console.error('Municipality search error:', error);
      toast({
        title: "Fel vid s√∂kning",
        description: error instanceof Error ? error.message : "Kunde inte hitta information om kommunen.",
        variant: "destructive"
      });
    } finally {
      setLoading(false);
    }
  };

  const getMockMunicipalityData = (address: string): MunicipalityResult | null => {
    const addressLower = address.toLowerCase();
    
    // Mock data for common Swedish municipalities
    const mockData: { [key: string]: MunicipalityResult } = {
      'stockholm': {
        municipality: 'Stockholms kommun',
        links: [
          {
            title: 'Ans√∂kan om livsmedelsregistrering',
            url: 'https://start.stockholm/starta-foretag/tillstand-och-anmalan/',
            description: 'Information om tillst√•nd och anm√§lan f√∂r livsmedelsverksamhet i Stockholm'
          },
          {
            title: 'Kontakta Stockholms stad - Livsmedel',
            url: 'https://kontakt.stockholm/ombud/miljohalsoskydd/',
            description: 'Kontaktuppgifter f√∂r milj√∂- och h√§lsoskydd g√§llande livsmedel'
          }
        ]
      },
      'g√∂teborg': {
        municipality: 'G√∂teborgs kommun',
        links: [
          {
            title: 'Starta f√∂retag - G√∂teborg Stad',
            url: 'https://goteborg.se/wps/portal/start/starta-foretag/',
            description: 'Information om att starta livsmedelsverksamhet i G√∂teborg'
          },
          {
            title: 'Milj√∂tillst√•nd och anm√§lningar',
            url: 'https://goteborg.se/wps/portal/start/miljotillstand/',
            description: 'Information om milj√∂tillst√•nd f√∂r livsmedelshantering'
          }
        ]
      },
      'malm√∂': {
        municipality: 'Malm√∂ kommun',
        links: [
          {
            title: 'Starta f√∂retag - Malm√∂ stad',
            url: 'https://malmo.se/Service/Foretagare-och-organisationer/Starta-foretag.html',
            description: 'V√§gledning f√∂r att starta livsmedelsf√∂retag i Malm√∂'
          },
          {
            title: 'Milj√∂- och h√§lsoskydd Malm√∂',
            url: 'https://malmo.se/Service/Foretagare-och-organisationer/Tillstand-och-anmalningar.html',
            description: 'Tillst√•nd och anm√§lningar f√∂r livsmedelsverksamhet'
          }
        ]
      },
      'uppsala': {
        municipality: 'Uppsala kommun',
        links: [
          {
            title: 'F√∂retagsst√∂d Uppsala',
            url: 'https://www.uppsala.se/boende-och-miljo/foretagare/',
            description: 'Information f√∂r f√∂retagare i Uppsala kommun'
          },
          {
            title: 'Milj√∂- och h√§lsoskydd',
            url: 'https://www.uppsala.se/boende-och-miljo/miljoskydd/',
            description: 'Milj√∂tillst√•nd och registreringar f√∂r livsmedel'
          }
        ]
      },
      '√§ngelholm': {
        municipality: '√Ñngelholms kommun',
        links: [
          {
            title: 'F√∂retagare - √Ñngelholms kommun',
            url: 'https://www.engelholm.se/foretagare/',
            description: 'Information f√∂r f√∂retagare i √Ñngelholms kommun'
          },
          {
            title: 'Milj√∂ och h√§lsa',
            url: 'https://www.engelholm.se/boende-miljo/miljoskydd/',
            description: 'Milj√∂tillst√•nd och h√§lsoskydd f√∂r livsmedelsverksamhet'
          }
        ]
      }
    };

    // Try to match the address to a known municipality
    for (const [city, data] of Object.entries(mockData)) {
      if (addressLower.includes(city)) {
        return data;
      }
    }

    return null;
  };

  const searchWithDirectCall = async () => {
    if (!tempApiKey) {
      throw new Error('Ingen tempor√§r API-nyckel angiven');
    }

    if (!tempApiKey.startsWith('pplx-')) {
      throw new Error('API-nyckeln m√•ste b√∂rja med "pplx-". Kontrollera att du har angett en riktig Perplexity API-nyckel.');
    }

    if (tempApiKey.length < 20) {
      throw new Error('API-nyckeln verkar f√∂r kort. En riktig Perplexity API-nyckel √§r cirka 40 tecken l√•ng.');
    }

    const response = await fetch('https://api.perplexity.ai/chat/completions', {
      method: 'POST',
      headers: {
        'Authorization': `Bearer ${tempApiKey}`,
        'Content-Type': 'application/json',
      },
      body: JSON.stringify({
        model: 'llama-3.1-sonar-small-128k-online',
        messages: [
          {
            role: 'system',
            content: 'Du √§r en expert p√• svenska kommuner och deras e-tj√§nster. Svara ENDAST med giltigt JSON utan andra tecken eller text.'
          },
          {
            role: 'user',
            content: `F√∂r adressen "${address}" i Sverige, hitta vilken kommun den tillh√∂r och ge mig aktuella l√§nkar till ans√∂kningsblanketter eller e-tj√§nster f√∂r livsmedelsregistrering/livsmedelstillst√•nd fr√•n den kommunen. 

Svara i exakt detta JSON-format:
{
  "municipality": "Kommunnamn",
  "links": [
    {
      "title": "Namn p√• tj√§nst/blankett",
      "url": "https://fullst√§ndig-url",
      "description": "Kort beskrivning"
    }
  ]
}`
          }
        ],
        temperature: 0.2,
        top_p: 0.9,
        max_tokens: 1000,
        return_images: false,
        return_related_questions: false,
        search_recency_filter: 'month',
        frequency_penalty: 1,
        presence_penalty: 0
      }),
    });

    if (!response.ok) {
      const errorText = await response.text();
      console.error('Direct API call failed:', response.status, errorText);
      
      if (response.status === 401) {
        throw new Error('API-nyckeln √§r ogiltig. Kontrollera att du har angett r√§tt Perplexity API-nyckel.');
      }
      throw new Error(`API-anrop misslyckades: ${response.status}`);
    }

    const data = await response.json();
    const content = data.choices[0].message.content;
    
    const parsedResult = JSON.parse(content);
    setResult(parsedResult);
    toast({
      title: "S√∂kning slutf√∂rd (direktanrop)",
      description: `Hittade information f√∂r ${parsedResult.municipality}`,
    });
  };

  return (
    <div className="space-y-4">
      {/* Temporary API Key Input (shown if search fails) */}
      {showApiKeyInput && (
        <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
          <div className="flex items-start gap-3">
            <div className="flex-shrink-0 w-8 h-8 bg-blue-100 rounded-full flex items-center justify-center">
              <span className="text-blue-600 font-semibold text-sm">i</span>
            </div>
            <div className="flex-1">
              <h4 className="font-semibold text-blue-900 mb-2">Ut√∂kad s√∂kning med API</h4>
              <p className="text-sm text-blue-800 mb-3">
                Denna plats finns inte i v√•r testdata. F√∂r att s√∂ka i alla svenska kommuner 
                beh√∂ver du en Perplexity API-nyckel (kostar ca 20-50 kr/m√•nad).
              </p>
              
              <div className="space-y-3">
                <div>
                  <label className="block text-xs font-medium text-blue-700 mb-1">
                    Perplexity API-nyckel (valfritt)
                  </label>
                  <Input
                    type="password"
                    placeholder="pplx-xxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxxx"
                    value={tempApiKey}
                    onChange={(e) => setTempApiKey(e.target.value)}
                    className={`text-sm ${
                      tempApiKey && !tempApiKey.startsWith('pplx-') 
                        ? 'border-red-300 focus:border-red-500' 
                        : ''
                    }`}
                  />
                  {tempApiKey && !tempApiKey.startsWith('pplx-') && (
                    <p className="text-xs text-red-600 mt-1">
                      ‚ö†Ô∏è API-nyckeln ska b√∂rja med "pplx-"
                    </p>
                  )}
                </div>
                
                <div className="flex items-center gap-2">
                  <Button 
                    variant="outline" 
                    size="sm"
                    onClick={() => setShowApiKeyInput(false)}
                  >
                    St√§ng
                  </Button>
                  <a 
                    href="https://www.perplexity.ai/settings/api" 
                    target="_blank" 
                    rel="noopener noreferrer"
                    className="text-xs text-blue-700 hover:text-blue-900 underline"
                  >
                    üîó Skaffa API-nyckel (ca 20kr/m√•n)
                  </a>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}

      {/* Address Search */}
      <div className="space-y-2">
        <div className="bg-green-50 border border-green-200 rounded-lg p-3">
          <p className="text-sm text-green-800">
            <strong>üéâ Testl√§ge aktivt!</strong> Prova med: Stockholm, G√∂teborg, Malm√∂, Uppsala eller √Ñngelholm
          </p>
        </div>
        
        <div className="flex gap-2">
        <div className="flex-1">
          <Input
            placeholder="Ange din fullst√§ndiga adress (ex. Drottninggatan 1, Stockholm)"
            value={address}
            onChange={(e) => setAddress(e.target.value)}
            onKeyDown={(e) => e.key === 'Enter' && searchMunicipality()}
          />
        </div>
        <Button 
          onClick={searchMunicipality}
          disabled={loading}
          size="sm"
        >
          {loading ? (
            <Loader2 className="w-4 h-4 animate-spin" />
          ) : (
            <Search className="w-4 h-4" />
          )}
        </Button>
        </div>
      </div>

      {/* Results */}
      {result && (
        <Card>
          <CardContent className="p-4">
            <div className="flex items-center gap-2 mb-3">
              <MapPin className="w-4 h-4 text-blue-600" />
              <h4 className="font-semibold text-sm">{result.municipality}</h4>
            </div>
            
            <div className="space-y-2">
              {result.links.map((link, index) => (
                <div key={index} className="border rounded-lg p-3 hover:bg-gray-50 transition-colors">
                  <div className="flex items-start justify-between gap-2">
                    <div className="flex-1">
                      <h5 className="font-medium text-sm text-blue-700">{link.title}</h5>
                      <p className="text-xs text-muted-foreground mt-1">{link.description}</p>
                    </div>
                    <Button
                      variant="ghost"
                      size="sm"
                      asChild
                      className="flex-shrink-0"
                    >
                      <a 
                        href={link.url} 
                        target="_blank" 
                        rel="noopener noreferrer"
                        className="flex items-center gap-1"
                      >
                        <ExternalLink className="w-3 h-3" />
                        <span className="text-xs">√ñppna</span>
                      </a>
                    </Button>
                  </div>
                </div>
              ))}
            </div>
            
            <p className="text-xs text-muted-foreground mt-3">
              Kontrollera alltid att informationen √§r aktuell p√• kommunens webbplats.
            </p>
          </CardContent>
        </Card>
      )}
    </div>
  );
};

export default MunicipalitySearch;